<template>
  <div class="container">
    <div class="activity_header">
      <!-- <img @click="back" src="../../assets/images/back.png" alt="">
      <span></span> -->
    </div>
    <div class="activity_container">
      <div class="container_top">
        <div class="container_top_title"></div>
        <div class="container_top_content"></div>
      </div>
      <div class="container_middle">
        <!-- <div class="container_middle_title"><img src="../../assets/images/more_img.png" alt=""></div> -->
        <Reward></Reward>
      </div>
      <div class="container_bottom">
        <div class="container_bottom_title"></div>
        <div class="container_bottom_content">
          1. APP
          <br>
          2. 
          <br>
          3. 1122551010
          <br>
          4. 10
          <br>
          5. 10%
          <br>
          6. 12
          <br>
          7. TV
          <br>
          8. APP >  > APP
          <br>
          9. 2020131
          <br>
          10.
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Reward from './ActivityPageType/Reward.vue'
export default {
  components: {Reward},
  data() {
    return {
      dataUrl: '',
      fromPage: ''
    }
  },
  props: ['jsonUrl'],
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.from(from.path)
    })
  },
  beforeRouteLeave (to, from, next) {
    next()
  },
  created() {
    var _hmt = _hmt || [];
    (function() {
      var hm = document.createElement("script");
      hm.src = "https://hm.baidu.com/hm.js?4926cf3d2b853ff341b5b083520ef6a1";
      var s = document.getElementsByTagName("script")[0];
      s.parentNode.insertBefore(hm, s);
    })();
    window.scrollTo(0, 0)
  },
  mounted() {
    this.dataUrl = this.$route.params.jsonUrl
  },
  methods: {
    back() {
      if (this.fromPage == '/index/recommend') {
        this.$router.push({
          path: this.fromPage
        })
      } else {
        let detailUrl = this.$route.params.jsonUrl
        this.$router.push({
          name: 'detail',
          params: {
            jsonUrl: detailUrl
          }
        })
      }
    },
    from(fromPage) {
      this.fromPage = fromPage
    }
  }
}
</script>

<style scoped>
.container {
  margin: 0px;
  padding: 0px;
}
.activity_header {
  margin: .34rem .3rem;
}
.activity_header>img {
  width: .48rem;
  height: .48rem;
}
.activity_header>span {
  font-size: .32rem;
  margin-left: .08rem;
  color: #636055;
  font-weight: 600;
  font-style: 'PingFang HK';
}
.activity_container {
  margin: 0;
}
.container_top_title {
  margin-left: .52rem;
  font-size: .32rem;
  color: #636055;
  font-weight: 600;
  font-style: 'PingFang HK';
}
.container_top_content {
  margin-top: .1rem;
  margin-left: .3rem;
  font-size: .26rem;
  color: #636055;
  font-style: 'PingFang HK';
}
.container_middle {
  margin-top: .2rem;
}
.container_middle_title {
  font-size: .24rem;
  color: #B49942;
  margin-left: .3rem;
  line-height: .44rem;
  font-style: 'PingFang HK';
}
.container_middle_title>img {
  width: .48rem;
  height: .48rem;
}
.container_bottom {
  margin-top: .4rem;
}
.container_bottom_title {
  color: #636055;
  font-size: .32rem;
  font-weight: 600;
  margin-left: .52rem;
}
.container_bottom_content {
  width: 6.7rem;
  margin: .2rem .3rem;
  color: #636055;
  font-size: .26rem;
}
</style>



